<template>
  <div class="home">
    <div class="header">
      <!-- <div class="address_map" @click="$router.push('/address')"> 为了传递city值，换成下面的写法，其中name值是路由router中js中对应的name值-->
      <div class="address_map" @click="$router.push({name:'address',params:{city:city}})">
        <i class="fa fa-map-marker"></i>
        <span>{{address}}</span>
        <i class="fa fa-sort-desc"></i>
      </div>
    </div>
    <!-- 搜索商家 -->
    <div class="search_wrap" :class="{'fixedview':showFilter}" @click="$router.push('/search')">
      <div class="shop_search">
        <i class="fa fa-search"></i>
        搜索商家 商家名称
      </div>
    </div>
    <div id="container">
      <!-- 轮播图 4s一动-->
      <mt-swipe :auto="4000" class="swiper">
        <mt-swipe-item v-for="(img,index) in swipeImgs" :key="index">
          <img :src="img" alt >
        </mt-swipe-item>
      </mt-swipe>
      <!-- 分类，手动滑动 -->
      <mt-swipe :auto="0" class="entries">
        <!-- 一共两页内容，先遍历分页 -->
        <mt-swipe-item v-for="(entry,i) in entries" :key="i" class="entry_wrap">
          <!-- 遍历每个分页的item -->
          <div class="foodentry" v-for="(item,index) in entry" :key="index">
            <div class="img_wrap">
              <img :src="item.image" alt>
            </div>
            <span>{{item.name}}</span>
          </div>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!-- 推荐商家 -->
    <div class="shoplist-title">推荐商家</div>
    <!-- 导航 -->
    <FilterView :filterData="filterData" @searchFixed="showFilterView" @update="update"></FilterView>
    <!-- 商家信息 -->
    <!-- mt标签是配合mint使用的上下滑动刷新效果的展示 -->
    <mt-loadmore
      :top-method="loadData"
      :bottom-method="loadMore"
      :bottom-all-loaded="allLoaded"
      :auto-fill="false"
      :bottomPullText="bottomPullText"
      ref="loadmore"
    >
      <div class="shoplist">
        <!-- 注意区分restaurants(数组)和item.restaurant(每个数据中的属性)的区别 -->
        <IndexShop v-for="(item,index) in restaurants" :key="index" :restaurant="item.restaurant" />
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Loadmore } from "mint-ui";//轮播图专用

import FilterView from "../components/FilterView.vue";
import IndexShop from "../components/IndexShop.vue";

export default {
    name: "home",
    data() {
      return {
        swipeImgs: [],//自动轮播图片
        entries: [],//手动滑动图片
        filterData: null,//商家信息
        showFilter: false,//是否显示搜索商家
        restaurants: [],//存放所有商家
        page: 1,//商家第一页
        size: 5,//5个商家
        allLoaded: false,
        bottomPullText: "上拉加载更多",
        data: null,//商家排序用的筛选数据
      }
    },
    computed: {
      address(){
        return this.$store.getters.address;
      },
      city(){//返回城市或者省份
        return (this.$store.getters.location.addressComponent.city || this.$store.getters.location.addressComponent.province);
      }
    },
    created(){
      this.getData();//轮播函数执行
    },
    methods: {
      getData() {
        //轮播数据（自动轮播+手动轮播）
        this.$axios("/api/profile/shopping").then(res => {//这里的接口是教育视频提供的
          // console.log(res.data);
          this.swipeImgs = res.data.swipeImgs;
          this.entries = res.data.entries;
        });
        //蒙版数据
        this.$axios("/api/profile/filter").then(res => {
          // console.log(res.data);
          this.filterData = res.data;
        })
        //商家信息
        this.loadData();
      },
      loadData() {
        //加载数据，上拉刷新
        this.page = 1;
        this.allLoaded = false;
        this.bottomPullText = "上拉加载更多";
        //商家信息-此处需要传递对应的参数，所以用post，注意：引用变量的时候要用`反引号来引
        this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.data).then(res => {
          // console.log(res.data);
          this.$refs.loadmore.onTopLoaded();
          this.restaurants = res.data;
        });
      },
      loadMore() {
        //下拉加载
        if(!this.allLoaded){
          //如果还有数据
          this.page++;
          //商家信息-此处需要传递对应的参数，所以用post，注意：引用变量的时候要用`反引号来引
          this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.data).then(res => {
            //加载完之后重新渲染
            this.$refs.loadmore.onBottomLoaded();
            if (res.data.length > 0) {
              res.data.forEach(item => {
                this.restaurants.push(item);
              });
              if (res.data < this.size) {
                //数据小于size(5条)说明后面没有了
                this.allLoaded = true;
                this.bottomPullText = "没有更多了哦";
              }
            } else {
              // 数据为空
              this.allLoaded = true;
              this.bottomPullText = "没有更多了哦";
            }
          });
        }
      },
      showFilterView(isShow){
        this.showFilter = isShow;
      },
      update(condition) {
        // console.log(condition);
        this.data = condition;
        // console.log(this.data);
        this.loadData();
      }
    },
    components: {
      FilterView,
      IndexShop,
    }
}
</script>

<style scoped>
.home {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}
.header,
.search_wrap {
  background-color: #009eef;
  padding: 10px 16px;
}
.header .address_map {
  color: #fff;
  font-weight: bold;
}
.address_map i {
  margin: 0 3px;
  font-size: 18px;
}
.address_map span {
  display: inline-block;
  width: 80%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.search_wrap .shop_search {
  /* margin-top: 10px; */
  background-color: #fff;
  padding: 10px 0;
  border-radius: 4px;
  text-align: center;
  color: #aaa;
}

.search_wrap {
  position: sticky;
  top: 0px;
  z-index: 999;
  box-sizing: border-box;
}
.swiper {
  height: 100px;
}
.swiper img {
  width: 100%;
  height: 100px;
}

.entries {
  background: #fff;
  height: 47.2vw;
  text-align: center;
  overflow: hidden;
}
.foodentry {
  width: 20%;
  float: left;
  position: relative;
  margin-top: 2.933333vw;
}
.foodentry .img_wrap {
  position: relative;
  display: inline-block;
  width: 12vw;
  height: 12vw;
}
.img_wrap img {
  width: 100%;
  height: 100%;
}
.foodentry span {
  display: block;
  color: #666;
  font-size: 0.32rem;
}
/* 推荐商家 */
.shoplist-title {
  display: flex;
  align-items: flex;
  justify-content: center;
  height: 9.6vw;
  line-height: 9.6vw;
  font-size: 16px;
  color: #333;
  background: #fff;
}
.shoplist-title:after,
.shoplist-title:before {
  display: block;
  content: "一";
  width: 5.333333vw;
  height: 0.266667vw;
  color: #999;
}
.shoplist-title:before {
  margin-right: 3.466667vw;
}
.shoplist-title:after {
  margin-left: 3.466667vw;
}

.fixedview {
  width: 100%;
  position: fixed;
  top: 0px;
  z-index: 999;
}

.mint-loadmore {
  height: calc(100% - 95px);
  overflow: auto;
}
</style>